import TextField from "components/atom/textField/TextField";
import TextEditor from "components/molecules/textEditor";
import React, { useEffect, useState } from "react";
import { URL_HEAD } from "shared/api";
import { BUTTONS } from "shared/constants";

function PagesField({
  editAPI = () => {},
  data = {},
  onImageChange = () => {},
}) {
  const [pageContent, setPageContent] = useState(
    data?.section_name ? data?.section_name : ""
  );
  const [title, setTitle] = useState(
    data?.section_content ? data?.section_content : ""
  );
  const [images, setImages] = useState(data?.files ? data?.files : []);
  return (
    <>
      <form autoComplete="off">
        <TextField
          label={"Title"}
          value={title}
          type="text"
          onChange={(e) => {
            setTitle(e.target.value);
          }}
        />
        {images?.length
          ? images?.map((i, idx) => (
              <>
                <img src={URL_HEAD + i?.file} alt="" height={120} key={i?.id} />
                <input
                  placeholder={"Upload Image"}
                  type="file"
                  // value={URL_HEAD + i?.file}
                  onChange={(e) => {
                    onImageChange(e?.target?.files[0], i?.id, i?.order);
                  }}
                />
              </>
            ))
          : null}
        <TextEditor setValue={setPageContent} value={pageContent} />
        <div className="text-center">
          <button
            className={`btn btn-primary`}
            onClick={(e) => editAPI(e, title, images, pageContent)}
          >
            {BUTTONS.UPDATE}
          </button>
        </div>
      </form>
    </>
  );
}

export default PagesField;
