import React, { useCallback, useEffect, useMemo, useState } from "react";
import "./style.scss";
import debounce from "lodash.debounce";
import { Images } from "shared/images";
import ReactSelect from "../ReactSelect";
import { docStatusUpdate, statusUpdate } from "./filterConsts";
import { useDispatch } from "react-redux";
import { cityListing, countryListing } from "redux/actions/auth";
import { FILTERS_ENUM, STATUS_OPTIONS } from "shared/constants";

function SearchBar({
  onChange,
  placeholder,
  keyword,
  setKeyword,
  style,
  filter = false,
  searchAPI = () => {},
  cityFilter,
  setCityFilter = () => {},
  countryFilter,
  setCountryFilter = () => {},
  status,
  setStatus = () => {},
  statusDoc,
  setStatusDoc = () => {},
}) {
  const dispatch = useDispatch();
  const [toggle, setToggle] = useState(false);
  const [city, setCity] = useState([]);
  const [country, setCountry] = useState([]);

  const filterButtons = [
    {
      title: "Country",
      data: country,
      id: FILTERS_ENUM.country,
      handleChange: (value) => {
        setCountryFilter(value?.value ? value?.value : "");
      },
    },
    {
      title: "City",
      data: city,
      id: FILTERS_ENUM.city,
      handleChange: (value) => {
        setCityFilter(value?.value ? value?.value : "");
      },
    },
    {
      title: "Activation Status",
      data: statusUpdate,
      id: FILTERS_ENUM.blocked,
      handleChange: (value) => {
        setStatus(value?.value);
      },
    },
    {
      title: "Document Status",
      data: docStatusUpdate,
      id: FILTERS_ENUM.blocked,
      handleChange: (value) => {
        setStatusDoc(value?.value);
      },
    },
  ];

  const searchApi = useCallback(
    (key) => {
      searchAPI(key);
    },
    [searchAPI]
  );

  const handleSearchChange = (e) => {
    setKeyword(e.target.value);
  };

  useEffect(() => {
    return () => {
      debouncedResults.cancel();
    };
  });

  const debouncedResults = useMemo(() => {
    return debounce(handleSearchChange, 300);
  }, []);

  useEffect(() => {
    searchApi(keyword);
  }, [keyword, searchApi]);

  const getListing = useCallback(() => {
    dispatch(
      cityListing(
        "",
        (res) => {
          const temp = res?.data?.length
            ? res?.data?.map((i, idx) => ({
                value: i.city,
                label: i.city,
              }))
            : [];
          temp.unshift({
            value: "",
            label: STATUS_OPTIONS.DEFAULT.title,
          });
          setCity(temp);
        },
        (err) => {
          // console.log(err?.data?.message, "ERROR CITY");
        }
      )
    );

    dispatch(
      countryListing(
        "",
        (res) => {
          const temp = res?.data?.length
            ? res?.data?.map((i, idx) => ({
                value: i.country,
                label: i.country,
              }))
            : [];
          temp.unshift({
            value: "",
            label: STATUS_OPTIONS.DEFAULT.title,
          });
          setCountry(temp);
        },
        (err) => {
          // console.log(err.data.message, "ERROR COUNTRY");
        }
      )
    );
  }, [dispatch, filter, toggle]);

  useEffect(() => {
    if (filter && toggle) getListing();
  }, [filter, getListing, toggle]);

  return (
    <div className="d-flex-col">
      <div className="d-flex searchbar ms-2">
        <input
          style={style}
          key="search-bar"
          placeholder={"Search here..."}
          onChange={debouncedResults}
          className="form-control"
        />
        {filter ? (
          <button
            className="btn btn-primary"
            onClick={() => {
              setToggle((prev) => !prev);
            }}
          >
            <img
              src={toggle ? Images.filterClose : Images.filter}
              alt="filter"
            />
          </button>
        ) : null}
      </div>
      {filter && toggle ? (
        <div className={`d-flex ${!toggle ? "d-none" : ""}`}>
          {filterButtons.map((i, idx) => {
            return (
              <div className="d-flex-col ">
                <label className="d-block text-capitalize">{i.title}</label>
                <ReactSelect
                  options={i.data}
                  handleChange={i.handleChange}
                  selectedOption={i.selected}
                  key={idx}
                  title={"Select"}
                />
              </div>
            );
          })}
        </div>
      ) : null}
    </div>
  );
}

export default SearchBar;
